.services {
	@include media($b1, $b2) {
		padding: 15rem 0 5rem;
	}
	@include media($b3, $b4) {
		padding: 32rem 0 14rem;
	}

	&__top {
		margin-bottom: 7rem;
		gap: 6rem;
	}

	&__header {
		@include red-strip('y');
	}

	&__list {
		@include media($b1, $b2) {
			padding: 4rem 0;
		}
		@include media($b3, $b4) {
			padding: 14rem 0;
			grid-template-columns: repeat(4, minmax(20rem, 1fr));
			grid-template-rows: repeat(2, minmax(20rem, 35.7rem));
			gap: 2.95rem;
		}
		&::after {
			content: '';
			position: absolute;
			height: 100%;
			background-image: url(#{$img-path}dots.svg);
			opacity: 0.3;
			z-index: -1;
			@extend %full-width;
		}
	}

	&__item {
		&:nth-child(1),
		&:nth-child(2) {
			@include media($b2, $b3, $b4) {
				background: transparent;
			}
		}
		&:nth-child(1) {
			grid-area: 1 / 1 / 2 / 3;
		}
		&:nth-child(2) {
			grid-area: 2 / 1 / 3 / 3;
		}
		&:nth-child(3) {
			grid-area: 1 / 3 / 3 / 5;
		}
	}

	&__icon,
	&__description {
		flex-basis: 50%;
		@include media($b1, $b2) {
			padding: 5rem;
		}
		@include media($b3, $b4) {
			padding: 10.5rem 8rem;
		}
	}

	&__icon {
		& > i {
			font-size: 19.4rem;
		}
	}

	&__arrow {
		&--right {
			@include media($b3, $b4) {
				transform: scaleX(-1);
			}
		}
	}

	&__line {
		margin-top: 5rem;
		height: 2px;
		&::after {
			content: '';
			position: absolute;
			height: 100%;
			width: 7px;
			right: 3rem;
			background: $section-color-dark;
		}
	}
}
