.form {
	&__button,
	&__field {
		height: 10rem;
	}

	&__field {
		box-shadow: 0px 3px 25px 0px rgba($accent-color, 0.16);
		&::before {
			content: '';
			transition: $base-animation-speed;
			position: absolute;
			left: 0;
			bottom: 0;
			height: 2px;
			width: 0;
			background-color: $accent-color;
			z-index: 1;
		}
		&:focus-within::before {
			width: 100%;
		}
		@include media($b3, $b4){
			max-width: 672px;
		};

		input {
			padding: 0 4rem;
			outline: 0;
			@include placeholder {
				color: $accent-text-color;
				font-size: 2.3rem;
				opacity: 0.6;
			}
		}
	}
}
