.user {
	min-height: 6rem;
	min-width: 13rem;
	padding: 5px;
	margin-left: auto;
	gap: 10px;
	@include media($b1, $b2) {
		font-size: 12px;
	}
	@include media($b3, $b4) {
		font-size: 16px;
	}

	&__img {
		&::after {
			@extend %circle;
			content: '';
			position: absolute;
			width: 1rem;
			height: 1rem;
			background: #3fa2f7;
			right: 8px;
			@extend %top-0;
		}
		@include media($b1, $b2) {
			width: 7rem;
			height: 7rem;
		}
		@include media($b3, $b4) {
			width: 5rem;
			height: 5rem;
		}
	}

	&__button {
		font-size: 5px;
	}
}
