@mixin red-strip($direction) {
	&:before {
		content: '';
		background: $accent-color;
	}
	@if ($direction == 'x') {
		& {
			padding-top: 4.5rem;
		}

		&:before {
			@extend %abs-align-x;
			height: 2.2rem;
			width: 10.3rem;
			top: 0;
		}
	} @else if ($direction == 'y') {
		& {
			padding-left: 4.5rem;
		}

		&:before {
			width: 2.2rem;
			height: 100%;
			@extend %abs-align-y;
			left: 0;
		}
	}
}
