.f {
	display: flex;
	&-mob {
		@include screen('mobile') {
			display: flex;
		}
	}
	&-tab {
		@include screen('tablet') {
			display: flex;
		}
	}
	&-desk {
		@include screen('desktop') {
			display: flex;
		}
	}

	&-col {
		flex-direction: column;
		&-desk {
			@include screen('desktop') {
				flex-direction: column;
			}
		}
		&-tab {
			@include screen('tablet') {
				flex-direction: column;
			}
		}
		&-mob {
			@include screen('mobile') {
				flex-direction: column;
			}
		}
	}

	&-row {
		flex-direction: row;
		&-mob {
			@include screen('mobile') {
				flex-direction: row;
			}
		}
		&-tab {
			@include screen('tablet') {
				flex-direction: row;
			}
		}
		&-desk {
			@include screen('desktop') {
				flex-direction: row;
			}
		}
	}

	&-wrap {
		flex-wrap: wrap;
	}

	&-center {
		display: flex;
		align-items: center;
		justify-content: center;
	}

	&-inline {
		display: inline-flex;
		&-mob {
			@include screen('mobile') {
				display: inline-flex;
			}
		}
		&-tab {
			@include screen('tablet') {
				display: inline-flex;
			}
		}
		&-desk {
			@include screen('desktop') {
				display: inline-flex;
			}
		}
	}
}

.g {
	display: grid;
	&-mob {
		@include screen('mobile') {
			display: grid;
		}
	}
	&-tab {
		@include screen('tablet') {
			display: grid !important;
		}
	}
	&-desk {
		@include screen('desktop') {
			display: grid;
		}
	}
}

.n {
	display: none;
	@include screen('mobile') {
		&-mob {
			display: none;
		}
	}
	@include screen('tablet') {
		&-tab {
			display: none;
		}
	}
	@include screen('desktop') {
		&-desk {
			display: none !important;
		}
	}
}

