.popup {
	padding: 25px 15px;
	left: -50%;
	background: rgba($accent-color, 0.85);
	clip-path: polygon(0 0, 100% 0, 85% 100%, 0% 100%);
	transition: 0.15s left cubic-bezier(0.77, 0, 0.175, 1);
	@include media($b1, $b2, $b3) {
		&.is-active {
			left: 0;
		}
	}
	@include media($b4){
		width: 30%;
		&.is-active {
			left: -30%;
		}
		
	};

	&__nav {
		margin: auto 0;
	}

	&__item {
		& + & {
			margin-top: 15px;
		}
	}

	&__link {
		font-size: 20px;
	}
}
