.download {
	@include media($b1, $b2) {
		padding: 15rem 0 5rem;
	}
	@include media($b3, $b4) {
		padding: 6rem 0;
	}	

	&__inner {
		gap: 5rem;
		& > * {
			flex-basis: 50%;
		}
	}

	&__text {
		margin-bottom: 7rem;

		& > P {
			padding-left: 4rem;
		}
	}

	&__heading {
		margin-bottom: 3rem;
		@include red-strip(y);
	}

	&__links {
		gap: 1rem;
		padding-left: 4rem;
		& > * {
			width: 22.6rem;
			height: 6.7rem;
		}
	}
}
