.restaurant {
	@include media($b1, $b2) {
		padding: 10rem 0 5rem;
	}
	@include media($b3, $b4) {
		padding: 10rem 0;
	}

	&__top {
		@include media($b1, $b2) {
			margin-bottom: 10rem;
		}
		@include media($b3, $b4) {
			margin-bottom: 20rem;
		}
	}

	&__dishes {
		gap: 3rem;
		@include media($b1, $b2) {
			margin-bottom: 7rem;
		}
		@include media($b3, $b4) {
			margin-bottom: 0;
		}
	}

	&__title {
		@include red-strip('y');
		font-size: 5rem;
		line-height: 1;
		margin-bottom: 25px;
	}

	&__wrapper {
		&::before {
			content: '';
			position: absolute;
			width: 41.5rem;
			height: 41.5rem;
			background: url(#{$img-path}dot.svg) no-repeat center / cover;
			@include media($b1, $b2){
				display: none;
			};
			@include media($b3, $b4){
				display: block;
			};
		}
		&:nth-child(even) {
			&:before {
				bottom: -4rem;
				right: -4rem;
			}
		}
		&:nth-child(odd) {
			&:before {
				top: -4rem;
				left: -4rem;
			}
		}
	}

	&__card {
		border-radius: 12px;
		@include screen('tablet') {
			max-width: 63.6rem;
		}
	}

	&__img,
	&__info {
		grid-column: 1/2;
		grid-row: 1/2;
	}

	&__info {
		padding: 5rem 2rem 2rem;
		@include screen('mobile') {
			padding: 5rem 5rem 2rem;
		}
		margin-top: auto;
		background: rgba($accent-color, 0.6);
	}

	&__dish {
		font-size: 5.3rem;
		margin-bottom: 1rem;
	}

	&__text {
		font-size: 1.7rem;
		margin-bottom: 2.5rem;
	}

	&__footer {
		gap: 2rem;
	}

	&__location {
		font-size: 18px;
		gap: 1rem;
		&:hover {
			text-decoration: underline;
		}

		& > span {
			font-size: 13px;
			@include screen('tablet') {
				font-size: 16px;
			}
		}
	}

	&__book {
		font-size: 2.1rem;
		border-bottom-right-radius: $base-border-radius;
		gap: 2rem;
		min-width: 19.5rem;
		min-height: 6.7rem;

		& > i {
			font-size: 2.5rem;
			margin-bottom: -3px;
		}
	}

	&__arrow {
		width: 43px;
		height: 43px;
	}

	&__more {
		font-size: 14px;
		@include screen('tablet') {
			width: 33.3%;
		}
	}
}
