.booking {
	padding: 27.3rem 0;
	&::before,
	&::after {
		z-index: 0;
		content: '';
		position: absolute;
	}
	&::before {
		width: 88rem;
		height: 100%;
		top: 0;
		left: -30rem;
		background: url(#{$img-path}booking-1.png) no-repeat center bottom / contain;
	}
	&::after {
		width: 40rem;
		height: 39rem;
		background: url(#{$img-path}booking-2.png) no-repeat center bottom / contain;
		right: -14rem;
		@extend %abs-align-y;
	}

	&__inner {
		padding: 12rem 15px 7rem;
		max-width: 127.5rem;
		margin: 0 auto;
	}

	&__content {
		max-width: 70rem;
		padding: 0 15px;
	}

	&__title {
		@include red-strip('x');
		margin-bottom: 6.5rem;
	}

	&__form {
		margin-bottom: 7rem;
		box-shadow: 0px 3px 25px 0px rgba($accent-color, 0.16);
		@include media($b1) {
			gap: 10px;
		}
		@include media($b2, $b3, $b4) {
			gap: 0;
		}
	}

	&__field,
	&__button {
		height: 10rem;
	}

	&__field {
		@include media($b2, $b3, $b4) {
			flex-basis: 75%;
		}
	}

	&__button {
		@include media($b2, $b3, $b4) {
			flex-basis: 25%;
			min-width: 15.6rem;
			min-height: 6.2rem;
		}
	}

	&__options {
		@include media($b1) {
			font-size: 13px;
		}
		@include media($b2, $b3, $b4) {
			font-size: 16px;
		}
	}

	&__label {
		gap: 1rem;
		.icon-triangle {
			font-size: 5px;
		}
	}

	&__date::-webkit-calendar-picker-indicator {
		position: absolute;
		margin: 0;
		top: 0;
		left: 0;
		width: 100%;
		background: none;
	}
}
