.header {
	padding: 2.5rem 0;
	font-size: 16px;
	transition: padding $base-animation-speed;
	@include media($b1, $b2, $b3) {
		box-shadow: 0px 1px 3px 0px rgba($accent-color, 0.25);
		background-color: $base-color;
	}
	@include media($b4) {
		background-color: initial;
		box-shadow: none;
	}

	&.is-fixed {
		z-index: 3;
		position: fixed;
		top: 0;
		left: 0;
		background-color: $section-color-dark;
		padding: 1rem 0;
		box-shadow: 0px 1px 3px 0px rgba($accent-color, 0.25);

		.header__logo {
			max-width: 11rem;
		}

		.hamburger {
			transform: scale(0.75);
			transition: transform 0.3s;
		}
	}

	&__hamburger {
		@include media($b1) {
			transform: scale(0.75);
			margin-bottom: -4px;
		}
		@include media($b2, $b3, $b4) {
			margin-bottom: -7px;
			transform: scale(1);
		}

		.hamburger-inner {
			height: 6px;
			&::before,
			&::after {
				height: 6px;
			}
		}
	}

	&__logo {
		transition: $base-animation-speed / 3;
		@include media($b1) {
			max-width: 11rem;
		}
		@include media($b1, $b2, $b3) {
			margin-left: 7px;
		}
		@include media($b2, $b3, $b4) {
			max-width: 15.5rem;
		}
		@include media($b4) {
			margin-left: 0;
		}
		&.is-visible {
			visibility: hidden;
		}
	}

	&__nav {
		flex-grow: 1;
		margin: 0 9.375%;
	}

	&__link {
		padding: 5px 7px;
		&:hover {
			background: $accent-color;
			color: $base-color-light;
		}
	}

	&__item {
		margin: 8px 0;
		&:not(:last-child) {
			margin-right: 6%;
		}
	}
}
