.foods {
	@include media($b1, $b2) {
		padding: 15rem 0;
	}
	@include media($b3, $b4) {
		padding: 16.5rem 0 28.5rem;
	}

	&__top {
		margin-bottom: 7rem;
	}

	&__heading {
		margin-bottom: 5rem;
	}

	&__list {
		grid-template-columns: repeat(3, minmax(20rem, 39.3rem));
		@include media($b3, $b4) {
			gap: 4rem;
		}
	}

	&__card {
		border-radius: 1.3rem;
		font-size: 14px;
	}

	&__img,
	&__text {
		@include media($b2, $b3, $b4) {
			flex-basis: 50%;
		}
	}

	&__text {
		color: #aeb2b4;
		padding: 3.5rem 2.5rem;

		&-top {
			margin-bottom: 3rem;
		}
	}

	&__title {
		font-size: 18px;
	}

	&__price {
		color: #f98169;
		font-size: 3rem;
		@include media($b2, $b3, $b4) {
			font-size: 2.5rem;
		}
	}
}
