.footer {
	font-size: 14px;
	@include media($b1, $b2) {
		padding: 15rem 0 5rem;
	}
	@include media($b3, $b4) {
		padding: 22.5rem 0 8.5rem;
	}

	&::before {
		content: '';
		position: absolute;
		width: 75rem;
		height: 90rem;
		left: 0;
		bottom: 0;
		background-image: url(#{$img-path}plant.png);
		background-size: contain;
		left: -5rem;
		bottom: -11rem;
		@include media($b1, $b2, $b3) {
			z-index: -1;
			opacity: 0.25;
		}
		@include media($b4) {
			z-index: initial;
			opacity: 1;
		}
	}

	&__top {
		max-width: 90rem;
	}

	&__heading {
		margin-bottom: 5rem;
	}

	&__text {
		margin-bottom: 8rem;
	}

	&__form {
		margin-bottom: 5rem;
	}

	&__form-input {
		border-radius: 2.5rem;
		box-shadow: 0px 2px 70px 0px rgba(161, 161, 161, 0.51);
		@include media($b1) {
			height: 8rem;
		}
		@include media($b2, $b3, $b4) {
			height: 6rem;
			max-width: 36.8rem;
		}

		& > input {
			padding-left: 3rem;
			padding-right: 7rem;
			font-size: 14px;

			@include placeholder {
				color: inherit;
				text-transform: capitalize;
			}
		}
	}

	&__button {
		width: 4rem;
		height: 4rem;
		right: 1.5rem;
		border-width: 0;

		& > i {
			font-size: 7px;
		}
	}

	&__nav {
		margin-bottom: 12rem;
	}

	&__nav-list {
		gap: 3rem;
	}

	&__nav-link:hover {
		text-decoration: underline !important;
	}

	&__socials {
		gap: 5rem;
		justify-content: flex-end;
	}

	&__socials-link:hover i {
		color: $accent-color;
		transition: $base-animation-speed;
	}
}
