.hero {
	&__inner {
		min-height: 640px;
		max-height: 2160px;
		@include media($b3, $b4) {
			height: 100vh;
			padding: 0;
			gap: 20px;
		}
		@media screen and (min-width: $page-max-width) {
			min-height: 1080px;
		}
	}

	&__info {
		margin-bottom: 25px;
		gap: 25px;
		@include media($b1, $b2) {
			padding: 80px 0 0;
		}
		@include media($b3, $b4) {
			padding: 0;
		}
	}

	&__title {
		@include media($b1) {
			font-size: 5rem;
		}
		@include media($b2, $b3, $b4) {
			font-size: 7.2rem;
		}
		& > span {
			line-height: 1;
			@include media($b3, $b4) {
				opacity: 0.04;
			}
		}
	}

	&__form {
		@include media($b1) {
			margin-bottom: 10px;
		}
		@include media($b2, $b3, $b4) {
			margin-bottom: 0;
		}
	}

	&__button,
	&__field,
	&__location {
		height: 10rem;
	}

	&__field {
		margin-bottom: 10px;
		& > input {
			@include media($b2, $b3, $b4) {
				padding: 0 21.5rem 0 5.5rem;
			}
		}
	}

	&__button,
	&__location {
		font-size: 3.1rem;
		padding: 5px;
	}

	&__button {
		@include media($b2, $b3, $b4) {
			font-size: 3.3rem;
			border-top-right-radius: $base-border-radius;
			border-bottom-right-radius: $base-border-radius;
			border-bottom-left-radius: 0;
			border-top-left-radius: 8.5rem;
			max-width: 20.6rem;
		}
	}

	&__location {
		border-width: 1px;
		gap: 3rem;
		@extend .button--dark;
		@include media($b1, $b2) {
			min-height: 4rem;
		}
		@include media($b2) {
			min-width: 30%;
			margin-left: 10px;
		}
		@include media($b3, $b4) {
			margin-left: 0;
			min-height: 8.8rem;
			border-top-left-radius: 0;
			border-top-right-radius: 0;
			min-width: 37.4rem;
			bottom: 1vh;
			border-top-right-radius: 4.4rem;
			border-bottom-right-radius: 4.4rem;
		}
	}

	&__imgs {
		min-height: 240px;
		@include media($b1, $b2) {
			padding: 21rem 0 10rem;
		}
		@include media($b3, $b4) {
			padding: 5rem 0 11rem;
			&::after {
				width: 1000%;
				border-top-right-radius: 0;
				border-bottom-right-radius: 0;
			}
		}

		&::before {
			content: '';
			@extend %abs-align-y;
			background: url(#{$img-path}dots.svg) repeat 0 0 / contain;
			@include media($b1, $b2) {
				width: 60%;
				left: -15px;
				height: 100%;
			}
			@include media($b3, $b4) {
				left: 0;
				width: 50%;
				height: 50%;
			}
		}

		&::after {
			content: '';
			position: absolute;
			border-top-right-radius: $base-border-radius;
			border-bottom-right-radius: $base-border-radius;
			right: -15px;
			top: 0;
			background: $accent-color;
			bottom: 0;
			left: 50%;
		}
	}

	&__slider {
		padding: 10px 0;
	}

	&__slide {
		height: inherit;
	}

	&__navigation {
		@include media($b3, $b4) {
			gap: 2rem;
			right: 2rem;
		}
	}

	&__prev,
	&__next {
		&:hover span {
			margin-left: -5rem;
		}
	}

	&__prev {
		@include media($b1, $b2) {
			border-color: $accent-color;
		}
		@include media($b3, $b4) {
			border-color: $base-color-light;
		}
	}

	&__next {
		border-color: $base-color-light;
	}
}
