.ta {
	&-center {
		text-align: center;
	}
	&-right {
		text-align: right;
	}
	&-left {
		text-align: left;
		&-mob {
			@include screen('mobile') {
				text-align: left;
			}
		}
		&-tab {
			@include screen('tablet') {
				text-align: left;
			}
		}
		&-desk {
			@include screen('desktop') {
				text-align: left;
			}
		}
	}
}

.tt {
	&-cap {
		text-transform: capitalize;
	}
	&-up {
		text-transform: uppercase;
	}
}

.td-none {
	text-decoration: none;
}