.slider {
	&__nav {
		padding: 0 5px;
		gap: 1.5rem;
		margin-top: 4rem;
		@include screen('tablet') {
			display: none;
		}
	}

	&__prev,
	&__next {
		width: 7rem;
		height: 7rem;
		border-width: 0.3rem;
		border-style: solid;
		font-size: 2rem;
		border-color: $accent-color;

		span {
			transition: all $base-animation-speed;
			display: inline-block;
			transform: scaleX(-1);
		}

		&:hover span {
			margin-left: -5rem;
		}
	}

	&__next {
		transform: scaleX(-1);
	}

	&__pagin {
		gap: 10px;
	}

	&__bullet {
		border-radius: 50%;
		display: inline-block;
		width: 10px;
		height: 10px;
		border: 1px solid $accent-color;
		&.active {
			background: $accent-color;
		}
	}
}
