
// width
.w {
	&-100 {
		width: 100%;
		&-mob {
			@include screen('mobile') {
				width: 100%;
			}
		}
		&-tab {
			@include screen('tablet') {
				width: 100%;
			}
		}
		&-desk {
			@include screen('desktop') {
				width: 100%;
			}
		}
	}

	&-50 {
		width: 50%;
		&-mob {
			@include screen('mobile') {
				width: 50%;
			}
		}
		&-tab {
			@include screen('tablet') {
				width: 50%;
			}
		}
		&-desk {
			@include screen('desktop') {
				width: 50%;
			}
		}
	}

	&-auto {
		width: auto;
		&-mob {
			@include screen('mobile') {
				width: auto;
			}
		}
		&-tab {
			@include screen('tablet') {
				width: auto;
			}
		}
		&-desk {
			@include screen('desktop') {
				width: auto;
			}
		}
	}
}


// height
.h {
	&-100 {
		height: 100% !important;
		&-mob {
			@include screen('mobile') {
				height: 100%
			}
		}
		&-tab {
			@include screen('tablet') {
				height: 100%
			}
		}
		&-desk {
			@include screen('desktop') {
				height: 100%
			}
		}
	}

	&-auto {
		height: auto;
	}
}

// max-width
.mw {
	&-100 {
		max-width: 100%;
		&-mob {
			@include screen('mobile') {
				max-width: 100%
			}
		}
		&-tab {
			@include screen('tablet') {
				max-width: 100%
			}
		}
		&-desk {
			@include screen('desktop') {
				max-width: 100%
			}
		}
	}

	&-50 {
		max-width: 50%;
		&-mob {
			@include screen('mobile') {
				max-width: 50%
			}
		}
		&-tab {
			@include screen('tablet') {
				max-width: 50%
			}
		}
		&-desk {
			@include screen('desktop') {
				max-width: 50%
			}
		}
	}
}

// overflow
.o {
	&-hidden {
		overflow: hidden;
		@include screen('mobile') {
			&-mob {
				overflow: hidden;
			}
		}
		@include screen('tablet') {
			&-tab {
				overflow: hidden;
			}
		}
		@include screen('desktop') {
			&-desk {
				overflow: hidden;
			}
		}
	}
	&-x-hidden {
		overflow-x: hidden;
	}
	&-visible {
		overflow: visible;
		@include screen('mobile') {
			&-mob {
				overflow: visible;
			}
		}
		@include screen('tablet') {
			&-tab {
				overflow: visible;
			}
		}
		@include screen('desktop') {
			&-desk {
				overflow: visible;
			}
		}
	}
}

// border-radius
.brs-base {
	border-radius: $base-border-radius;
}

.circle,
%circle {
	border-radius: 50%;
}

