h1, h2, h3, h4, h5, h6 {
	color: $accent-text-color;
	font-family: $sans-serif-1;
	font-weight: 500;
	line-height: 1;
};

.h1{
	font-size: 15rem;
	@include screen('mobile'){
		font-size: 20rem;
	};

}

.h2{
	font-weight: 600;
	font-size: 5.5rem;
	@include screen('tablet'){
		font-size: 7.5rem;
	};

}
.h3{
	font-size: 49px;
}

.h4{
	font-size: 3.8rem;
}

.h5{
	font-size: 2.9rem;
	font-weight: $base-font-weight;
}