.abs {
	position: absolute;
	&-mob {
		@include screen('mobile') {
			position: absolute;
		}
	}
	&-tab {
		@include screen('tablet') {
			position: absolute;
		}
	}
	&-desk {
		@include screen('desktop') {
			position: absolute;
		}
	}
}

.rel {
	position: relative;
	&-mob {
		@include screen('mobile') {
			position: relative;
		}
	}
	&-tab {
		@include screen('tablet') {
			position: relative;
		}
	}
	&-desk {
		@include screen('desktop') {
			position: relative;
		}
	}
}

.static {
	position: static;
	&-mob {
		@include screen('mobile') {
			position: static;
		}
	}
	&-tab {
		@include screen('tablet') {
			position: static;
		}
	}
	&-desk {
		@include screen('desktop') {
			position: static;
		}
	}
}

.fixed {
	position: fixed;
}

.top-0,
%top-0 {
	top: 0;
}

.bottom-0 {
	bottom: 0;
}

.left-0 {
	left: 0;
}

.left-auto {
	left: auto;
	&-mob {
		@include screen('mobile') {
			left: auto;
		}
	}
	&-tab {
		@include screen('tablet') {
			left: auto;
		}
	}
	&-desk {
		@include screen('desktop') {
			left: auto;
		}
	}
}

.right-0 {
	right: 0;
	&-mob {
		@include screen('mobile') {
			right: 0;
		}
	}
	&-tab {
		@include screen('tablet') {
			right: 0;
		}
	}
	&-desk {
		@include screen('desktop') {
			right: 0;
		}
	}
}

.abs-align-xy,
%abs-align-xy {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
}

.abs-align-y,
%abs-align-y {
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
}

.abs-align-x,
%abs-align-x {
	position: absolute;
	left: 50%;
	transform: translateX(-50%);
}