.clr {
	&-accent {
		color: $accent-color;
	}
	&-light-base {
		color: $base-color-light;
		@include screen('tablet'){
			&-tab {
				color: $base-color-light;
			}
		};
	}
	&-text-accent {
		color: $accent-text-color;
	}

	&-dark {
		color: $base-color-dark;
	}
}

.bg {
	&-accent {
		background: $accent-color;
	}
	&-base {
		background: $base-color;
	}
	&-light-base {
		background: $base-color-light;
	}
	&-section-dark {
		background: $section-color-dark;
	}
}