.dropdown {
	left: 0;
	border-bottom-left-radius: $base-border-radius;
	border-bottom-right-radius: $base-border-radius;
	visibility: hidden;
	opacity: 0;
	padding: 10px;
	transform: translateY(-3em);
	transition: all $base-animation-speed ease 0s, visibility 0s linear $base-animation-speed;
	top: 100%;

	&.active {
		visibility: visible;
		opacity: 1;
		transform: translateY(0);
		transition-delay: 0s, 0s, $base-animation-speed;
	}

	&.active + &__arrow {
		transform: rotate(180deg);
	}

	& > li:not(:last-child) {
		margin-bottom: 15px;
	}

	&__link {
		text-decoration: none;
	}
}
